import { useEffect, useMemo, useState } from 'react';

import { ResponseModalContent } from '@common/utils/menu-config';
import { emitMenuResponse } from '@common/utils/menu-window-provider';
import { getUrlParams, Router, withRouter } from '@common/utils/router';
import { currentOFIdentity, getOFWindow } from '@common/utils/window';

import { t } from '@common/api/i18next';

import GlobalOverflowHidden from '@common/components/GlobalOverflowHidden/GlobalOverflowHidden';

import {
    type ResponseModalActionHandler,
    type ResponseModalValidateHandler,
    ResponseModal
} from '@enterprise/components/ModalContent/ResponseModal';

import { getCurrentSync } from '@client-platform/index';
import { BrowserTypeContext } from '@common/hooks/context';

const parseStringifiedContent = (content: string | undefined) => {
    try {
        return content && JSON.parse(content);
    } catch (err) {
        console.error(err);
        return null;
    }
};

const getEntitiesByType = (type: 'page' | 'workspace') => {
    if (type === 'page') {
        return getCurrentSync().Storage.getPages();
    }
    return getCurrentSync().Storage.getWorkspaces();
};

type ResponseModalPageProps = {
    router: Router;
};
const ResponseModalPage: React.FC<ResponseModalPageProps> = ({ router }) => {
    const [urlPath, setUrlPath] = useState('');
    // need to set via effect in order to avoid SSR-client render mismatch
    useEffect(() => {
        setUrlPath(router.asPath);
    }, [router.asPath]);
    const { content, isEnterprise: isParentEnterprise } = useMemo(() => getUrlParams(urlPath), [urlPath]);
    const isEnterprise = isParentEnterprise === 'true';
    const parsedContent: ResponseModalContent = parseStringifiedContent(content);

    const handleAction: ResponseModalActionHandler = async ({ name, data }) => {
        await emitMenuResponse({ actionName: name, data });
        getOFWindow(currentOFIdentity).blur();
    };

    const handleValidation: ResponseModalValidateHandler = async ({ textInputValue }) => {
        if (!textInputValue) {
            return t('saveModal.enterValue');
        }

        const entities = await getEntitiesByType(parsedContent.closeType);
        const targetEntityExists = entities.some((entity) => entity.title === textInputValue);

        if (targetEntityExists) {
            return t('saveModal.alreadyExists', { title: textInputValue });
        }

        return '';
    };

    if (parsedContent) {
        const { title, body, buttons, textInputOptions, invocationId } = parsedContent;
        return (
            <BrowserTypeContext.Provider value={{ isEnterprise }}>
                <GlobalOverflowHidden />
                <ResponseModal
                    title={title}
                    body={body}
                    buttons={buttons}
                    onAction={handleAction}
                    validate={handleValidation}
                    textInputOptions={textInputOptions}
                    invocationId={invocationId}
                />
            </BrowserTypeContext.Provider>
        );
    }
    return null;
};

export default withRouter(ResponseModalPage);
